import React, { useState } from "react";
import { v4 as uuidv4 } from "uuid";
import "../css/Analysis.css";

const Analysis = ({ todoLists }) => {
  let top3 = [];
  let oT = 0;

  function secondsToHhMmSs(inputTime) {
    let hours = String(Math.floor(inputTime / 3600)).padStart(2, "0");
    let minutes = String(Math.floor((inputTime - 3600 * hours) / 60)).padStart(
      2,
      "0"
    );
    let seconds = String(inputTime - 3600 * hours - 60 * minutes).padStart(
      2,
      "0"
    );
    const HhMmSs = hours + ":" + minutes + ":" + seconds;
    return HhMmSs;
  }
  // 여기서 각 subject 당 시간 더하기
  function reducer(acc, cur) {
    let s = cur.subject;
    if (s in acc) {
      acc[s] += cur.studyTime;
    } else {
      acc[s] = cur.studyTime;
    }
    return acc;
  }

  let counted = todoLists.reduce(reducer, {});

  function time_descending(a, b) {
    let aT = counted[a];
    let bT = counted[b];
    return aT > bT ? -1 : 1;
  }
  todoLists.forEach((element) => {
    let nT = oT + element.studyTime;
    oT = nT;
  });
  const sorted = Object.keys(counted).sort(time_descending).slice(0, 3); // 여기서 Top3 key 값 분류
  sorted.forEach((key) => {
    let eT = secondsToHhMmSs(counted[key]);
    const no = {
      id: uuidv4(),
      subject: key,
      studyTime: eT,
      rT: (counted[key] / oT).toFixed(2) * 100,
    };
    top3.push(no);
  });

  const TotalTime = secondsToHhMmSs(oT);
  return (
    <>
      <div className="analysis-total-title">
        <h1 className="anlysis-title-h1">총 누적 소요시간</h1>
        <h1 className="analysis-total-time">{TotalTime}</h1>
      </div>
      <div className="analysis-top3-container">
        <h1 className="analysis-top3-title">Top 3 카테고리</h1>
        <table className="analysis-top3-table-container">
          <thead>
            <tr className="analysis-thead">
              <th className="analysis-subject-column">카테고리</th>
              <th className="anlaysis-studyTime-column">누적 소요시간</th>
            </tr>
          </thead>
          <tbody>
            {top3.map((el) => (
              <tr className="analysis-top3-element">
                <td>{el.subject}</td>
                <td>
                  {el.studyTime} ({el.rT}%)
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </>
  );
};

export default Analysis;
