import { dbService } from "fbase";
import React, { useEffect, useState } from "react";
import { v4 as uuidv4 } from "uuid";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faExclamationCircle } from "@fortawesome/free-solid-svg-icons";
import HistoryTable from "./HistoryTable";
import "../css/History.css";

const History = ({ todoLists, userObj }) => {
  const [filterTarget, setFilterTarget] = useState("");
  const [sortTodo, setSortTodo] = useState(todoLists);
  const [oT, setoT] = useState(0);

  function secondsToHhMmSs(inputTime) {
    let hours = String(Math.floor(inputTime / 3600)).padStart(2, "0");
    let minutes = String(Math.floor((inputTime - 3600 * hours) / 60)).padStart(
      2,
      "0"
    );
    let seconds = String(inputTime - 3600 * hours - 60 * minutes).padStart(
      2,
      "0"
    );
    const HhMmSs = hours + ":" + minutes + ":" + seconds;
    return HhMmSs;
  }

  useEffect(() => {
    let thisT = 0;
    if (filterTarget !== "") {
      const filteredTodoLists = todoLists.filter(
        (todoList) => todoList.subject === filterTarget
      );
      filteredTodoLists.forEach((element) => {
        let nT = thisT + element.studyTime;
        thisT = nT;
      });
      setoT(thisT);
      setSortTodo(filteredTodoLists);
    } else {
      todoLists.forEach((element) => {
        let nT = thisT + element.studyTime;
        thisT = nT;
      });
      setoT(thisT);
      setSortTodo(todoLists);
    }
  }, [filterTarget, todoLists]);

  let baseArray = [];
  let filterArray = [];

  const onClick = (event) => {
    const clickedFilter = event.target.innerText;
    setFilterTarget(clickedFilter);
  };
  const onReset = () => {
    const ok = window.confirm("히스토리를 전부 리셋하시겠습니까?");
    if (ok) {
      dbService
        .collection("completed")
        .doc("sortByUser")
        .collection(`${userObj.uid}`)
        .get()
        .then((querySnapshot) => {
          querySnapshot.docs.forEach((snapshot) => {
            snapshot.ref.delete();
          });
        });
    }
  };
  const unfilter = (event) => {
    setFilterTarget("");
  };
  todoLists.map((todo) => {
    baseArray.push(todo.subject);
  });
  const targetSubject = [...new Set(baseArray)];
  targetSubject.map((doc) => {
    const filterObj = {
      id: uuidv4(),
      subject: doc,
    };
    filterArray.push(filterObj);
  });

  const TotalTime = secondsToHhMmSs(oT);
  return (
    <>
      <div>
        <h1 className="todo-title">History</h1>
        <div className="todo-filter-div">
          <button className="todo-filter-btn" onClick={unfilter}>
            전체
          </button>
          {filterArray.map((doc) => (
            <button className="todo-filter-btn" onClick={onClick} key={doc.id}>
              {doc.subject}
            </button>
          ))}
        </div>
        <div className="history-description">
          <h1>해당 카테고리 누적 소요 시간 : {TotalTime}</h1>
          <button className="reset-btn" onClick={onReset}>
            <FontAwesomeIcon icon={faExclamationCircle} /> Reset ALL
          </button>
        </div>
        <div className="history-table-container">
          <table className="history-table">
            <thead className="history-thead fixedHeader">
              <tr>
                <th className="history-task-column">완료한 일</th>
                <th className="history-category-column">카테고리</th>
                <th className="history-format-column">형식</th>
                <th className="history-due-column">완료일</th>
                <th className="history-studytime-column">소요 시간</th>
              </tr>
            </thead>
            <tbody>
              {sortTodo.map((todoList) => (
                <HistoryTable
                  userObj={userObj}
                  key={todoList.id}
                  todoObj={todoList}
                />
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </>
  );
};

export default History;
