import React, { useEffect, useState } from "react";
import "../css/HistoryTable.css";

const HistoryTable = ({ userObj, todoObj }) => {
  const [convertTime, setConvertTime] = useState("00:00:00");

  function secondsToHhMmSs(inputTime) {
    let hours = String(Math.floor(inputTime / 3600)).padStart(2, "0");
    let minutes = String(Math.floor((inputTime - 3600 * hours) / 60)).padStart(
      2,
      "0"
    );
    let seconds = String(inputTime - 3600 * hours - 60 * minutes).padStart(
      2,
      "0"
    );
    const HhMmSs = hours + ":" + minutes + ":" + seconds;
    return HhMmSs;
  }
  useEffect(() => {
    const convertedTime = secondsToHhMmSs(todoObj.studyTime);
    setConvertTime(convertedTime);
  }, [todoObj.studyTime]);
  return (
    <>
      <tr className="history-row">
        <td className="history-table-task-column">{todoObj.work}</td>
        <td className="history-table-subject-column">{todoObj.subject}</td>
        <td className="history-table-format-column">{todoObj.format}</td>
        <td className="history-table-sumbitAt-column">{todoObj.submitAt}</td>
        <td className="history-table-studyTime-column">{convertTime}</td>
      </tr>
    </>
  );
};

export default HistoryTable;
